.App {
  text-align: center;
}

/*Navigation CSS*/
header{
  display: flex;
  align-items: center;
  background: #014F01;
  position: sticky;
  width: 100vw;
  height: 55px;
  left: 0px;
  z-index: 1;
}

header>img{
  position: relative;
  left: -45px;
}

header>h1{
  font-family: 'Bebas Neue';
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 48px;
  position: relative;
  left: -95px;
  color: #000;
  z-index: 2;
}



.navigation{
  display: flex;
  width: auto;
  height: auto;
  position: absolute;
  right: 0;
  background: #014F01;
}

.navbar-nav>li{
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: auto;
  font-weight: 400;
  font-size: 25px;
  color: #fff;
  border-left: solid #000 2px;
}

.nav-toggler{
  background: #014F01;
  border: none;
  position: absolute;
  right: -40px;
}

.nav-item>a{
  height: 100%;
  width: 100%;
  text-align: center;
  color: #fff;
  transition: 0.8s;
}

.nav-item>a:hover{
  color: #000;
}

.navbar-nav>li:hover{
  cursor: pointer;
  background: #fff;
  color: #000;
  border-left: none;
}

/*Cards*/
.card-service{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 60px 100px 60px; 
  position: relative;
  top: -50px;
}



.outer-circle{
  width: 170px;
  height: 170px;
  border-radius: 85px;
  position: relative;
  top: 85px;
  left: 30px;
  background: #fff;
  justify-content: center;
  display: flex;
  align-items: center;
}

.icon-position{
  position: relative;
  margin-left: 70%;
  top: -36.5%;
}

/* style={{color:data.checked?"#014F01":null, position:"absolute", top: "6%", left: 175}} */

.inner-circle{
  width: 120px;
  height: 120px;
  border-radius: 85px;
  position: relative;
  background: #014F01;
  justify-content: center;
  display: flex;
  align-items: center;
}

.card-contents{
  background: #e9e6e9 ;
  width: 240px;
  height: 350px;
  transition: 1s;
}

.card-contents:hover{
  box-shadow: 10px 10px rgba(0, 0, 0, 0.4) ;
}

.card-contents>h4{
  position: relative;
  top: 100px;
  text-align: center
}

.card-contents>ul{
  position: relative;
  top: 100px
}


.card-product{
  width: 200px;
  height: 120px;
  margin: 30px;
  overflow: hidden;
  background: #014F01;
}

.product-overlay{
  width: 100%;
  height: 125px;
  background: transparent;
  color: transparent;
  position: relative;
  top: -200%;
  transition: 0.6s;
}

.product-overlay>h4{
  position: relative;
  margin-left: 5%;
  top: 5%;
  font-weight: 100;
  width: 80%;
  height: 40%;
  font-size: 20px;
}

.mobile-alignment{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.product-overlay>p{
  position: relative;
  top: -10%;
  margin-left: 10%;
  font-size: 14px;
  margin-bottom: -2%;
}

.product-overlay:hover{
  background: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.filter{
  background:  #014F01;
  width: 100%;
  height: 50.81px;
  display: flex;
  align-items: center;
}

.filter_button{
  background: #fff;
  width: 100px;
  height: 50%;
  border: none;
  border-radius: 25px;
  margin: 0 15px;
  transition: 0.5s;
}

.filter_button:hover{
  box-shadow: inset rgba(0, 0, 0, 0.4) 3px 3px;
  cursor: pointer;
}

.standard{
  background:  #014F01;
  color: #fff;
  width: 150px;
  height: 40px;
  border-radius: 25px;
  border: none;
  font-size: 20px;
  /* font-weight: bold; */
  transition: 0.8s;
}
.standard:hover{
  box-shadow: 3px 3px rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

