.navbar-toggler{
  position: relative;
  right: 5%;
}

.button1{
  position: relative;
  width: auto;
  height: auto;
  top: -40px;
  left: 30%;
}

.button2{
  position: absolute;
  left: 76%;
  top: 310%;
