  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
}
.textinput{
  padding-left: 20px;
  width: 300px;
  height: 40px;
  border-radius: 25px;
  border: none;
  font-size: 20px;
  color: #000;
  margin: 10px 0;
}

#address{
  padding-left: 20px;
  padding-top: 20px;
  width: 290px;
  height: 150px;
  border-radius: 25px;
  border: none;
  font-size: 23px;
  color: #000;
  margin: 10px 0;
  resize: none;
}

#service{
  padding-left: 20px;
  padding-right: 30px;
  width: 320px;
  height: 40px;
  border-radius: 25px;
  border: none;
  font-size: 20px;
  color: #000;
  margin: 10px 0;
}

.orderBox_left{
  position: fixed;
  background: #014F01;
  height: 100%;
  width: 50vw;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 3;
  transition: 1s;
  color: #fff;
  text-decoration: underline;
}

.orderBox_right{
  position: fixed;
  background: #014F01;
  height: 100%;
  width: 50%;
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 3;
  transition: 1s;
  color: #fff;
  text-decoration: underline;
}

.mini{
  width:auto;
  height: 20px;
  color: #000;
  background: #fff;
  display: flex;
  align-items: center;
  border-radius: 25PX;
  margin: 3px 10px;
}

.mini>p{
  margin-left: 20px;
  margin-right: 20px;
  text-decoration: none;
}

.mini>i{
  margin-right: 10px;
  cursor: pointer;
}
.product-box{
  background: #fff;
  width: 100%;
  height: 110%;
  display: flex;
  flex-direction: row;
  overflow-x: scroll
}

.product-box::-webkit-scrollbar{
  background-color: #fff;
}

.product-box::-webkit-scrollbar-thumb{
  background-color: #ccc;
  border-radius: 25px;
}

.product-box::-webkit-scrollbar-thumb:hover{
  background-color: #999;
  cursor: pointer;
}

.product-box>div{
  width: 250px;
  height: 100%;
}

.segment{
  width: 100%;
  height: 40px;
  display: flex;
}

.segment>div{
