}

.info-box>h2{
  transform: scale(0.7);
  margin-top: -1px;
  margin-left: -13px;
}

.info-box>p{
  transform: scale(0.7);
  margin-top: -40px;
  margin-left: -8px;
}

.about-body{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cover{
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.7;
  color: #fff;
  position: relative;
  top: -300%
}
.title{
  font-size: 60px;
  position: relative;
  top: 80px;
  margin-left: 180px;
}
.text{
  font-size: 30px;
  position: relative;
  top: 80px;
  margin-left: 250px;
  width: 280px;
}

.tab{
  width: 100%;
  height: 80px;
  background-color: #014F01;
  color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.open{
  display: block;
}

.close{
  display: none;
}

