/*Slideshow*/
.show1{
  background:url("./assets/ian-battaglia.jpg");
  background-position: center;
  background-size: 100%;
  width: 100%;
  height: 100%;
  animation-name: slide1;
  animation-duration: 50s;
  animation-iteration-count: infinite;
}
.show2{
  background:url("./assets/alexandre-debieve.jpg");
  background-position: center;
  background-size: 100%;
  width: 150%;
  height: 100%;
  position: relative;
  top: -100%;
  animation-name: slide2;
  animation-duration: 50s;
  animation-iteration-count: infinite;
}
.show3{
  background:url("./assets/jackson-david.jpg");
  background-position: center;
  background-size: 100%  auto;
  width: 100%;
  height: 100%;
  position: relative;
  top: -200%;
  animation-name: slide3;
  animation-duration: 50s;
  animation-iteration-count: infinite;
}

@keyframes slide1{
  0%{
    background-size: 100%;
  }
  30%{
    opacity: 1;
  }
  40%{
    opacity: 0
  }
  90%{
    background-size: 200%;
  }
  100%{
    opacity: 1;
    background-size: 100%;
  }
}

@keyframes slide2{
  0%{
    right: 0px;
    opacity: 0;
  }
  40%{
    opacity: 1;
  }
  60%{
    opacity: 1;
  }
  70%{
    opacity: 0;
  }
  100%{
    opacity: 0;
    right: 200px;
  }
}

@keyframes slide3{
  0%{
    opacity: 0;
  }
  60%{
    opacity: 0;
  }
  70%{
    opacity: 1;
  }
  90%{
    opacity: 1;
  }
  100%{
    opacity: 0;
  }
}

